<!-- 分组玫瑰图
 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>分组玫瑰图</h3>
      <div id="container"></div>
    </div>
  </div>
</template>

<script>
import { Rose } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = [
      {
        type: '分类一',
        value: 27,
        user: '用户一'
      },
      {
        type: '分类二',
        value: 25,
        user: '用户一'
      },
      {
        type: '分类三',
        value: 18,
        user: '用户一'
      },
      {
        type: '分类四',
        value: 15,
        user: '用户一'
      },
      {
        type: '分类五',
        value: 10,
        user: '用户一'
      },
      {
        type: '其它',
        value: 5,
        user: '用户一'
      },
      {
        type: '分类一',
        value: 7,
        user: '用户二'
      },
      {
        type: '分类二',
        value: 5,
        user: '用户二'
      },
      {
        type: '分类三',
        value: 38,
        user: '用户二'
      },
      {
        type: '分类四',
        value: 5,
        user: '用户二'
      },
      {
        type: '分类五',
        value: 20,
        user: '用户二'
      },
      {
        type: '其它',
        value: 15,
        user: '用户二'
      }
    ]

    // 分组玫瑰图
    const rosePlot = new Rose('container', {
      data,
      xField: 'type',
      yField: 'value',
      isGroup: true,
      // 当 isGroup 为 true 时，该值为必填
      seriesField: 'user',
      radius: 0.9,
      label: {
        offset: -15
      },
      interactions: [
        {
          type: 'element-active'
        }
      ]
    })

    rosePlot.render()
  },
  methods: {},
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



